'use client';

import { useEffect , useState } from "react";
import { LogInAPI } from "@/features/Authentication/authSlice";
import { useDispatch } from "react-redux";
import { useRouter } from "next/navigation";

export default function Home() {

  const dispatch = useDispatch();
  const router = useRouter();

  const initialState = {
    username : "",
    password : ""
  }

  const [formData , setFormData] = useState(initialState);

  const HandleChange = (e) =>{

    const {value , name} = e.target;

    setFormData({
      ...formData,
      [name] : value,
    })
  }


  const HandleLogIn = async(e)=> {

    e.preventDefault();

    try {
      
      await dispatch(LogInAPI(formData)).unwrap();
      
      router.replace("/dashboard")

    } catch (err) {
      alert(err?.error || "Errore di autenticazione");
    }

  }



  return (
    
    <div className="flex justify-center items-center min-h-screen bg-slate-900">
    
      <div className="p-8 bg-gray-800 rounded-md shadow-md text-white">
        
        <h1 className="text-2xl mb-4 font-bold">Login</h1>
        
        <form onSubmit={HandleLogIn} className="flex flex-col gap-4">

          <input name="username" value={formData.username} onChange={HandleChange} type="text" placeholder="Username"  className="p-2 rounded bg-gray-700 text-white" />
          <input name="password" value={formData.password} onChange={HandleChange} type="password"  placeholder="Password" className="p-2 rounded bg-gray-700 text-white" />
          <button  type="submit" className="p-2 bg-blue-500 rounded hover:bg-blue-600" > Login </button>
    
        </form>
    
      </div>
    
    </div>

  );
}
